<template>
  <div class="edit_container">
    <el-upload
      class="avatar-uploader"
      :action="$baseUrl + '/saveMdImage'"
      :show-file-list="false"
      :data="Data"
      :on-success="uploadSuccess"
      :on-error="uploadError"
      :before-upload="beforeUpload">
    </el-upload>
    <quill-editor 
        v-model="content" 
        ref="myQuillEditor" 
        :options="editorOption" 
        @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
        @change="onEditorChange($event)">
    </quill-editor>
  </div>  
</template>

<script>
const toolbarOptions = [
  ['bold'],
  ['image']
]
export default {
  name: 'edit_container',
  data(){
     return {
            content: '',
            Data:{
              token: ''
            },
            editorOption: {
              placeholder: '',
              modules: {
                toolbar: {
                  container: toolbarOptions,
                  handlers:{
                    'image': function(value) {
                      if (value) {
                        document.querySelector('.avatar-uploader input').click()
                      } else {
                        this.quill.format('image', false);
                      }
                    }
                  }
                }
              }
            }
        }
  },
  computed: {
      editor() {
          return this.$refs.myQuillEditor.quill;
      },
  },
  mounted(){
    var token = this.getToken();
    if (token) {
      this.Data.token = token;
    }
  },
  methods: {
    onEditorReady(editor) { // 
    },
    onEditorBlur(){
    }, // 
    onEditorFocus(){}, // 
    onEditorChange(){
      this.$emit('newsEvent',this.content);
    }, // 
     // 
    beforeUpload() {
    // loading
      this.quillUpdateImg = true
    },
    uploadSuccess(res, file) {
      // res
      // 
      console.log(res);
      let quill = this.$refs.myQuillEditor.quill
      // 
      if (res.code == 200 ) {
      // 
        let length = quill.getSelection().index;
      //  res.url
        quill.insertEmbed(length, 'image', this.$ImgUrl + '/markdown/' + res.data)
      // 
        quill.setSelection(length + 1)
      } else {
        this.$message.error('')
      }
      // loading
      this.quillUpdateImg = false
    },
    // 
    uploadError() {
    // loading
      this.quillUpdateImg = false
      this.$message.error('')
    },
    //token
    getToken(){
      var theToken = sessionStorage.getItem('token');
      return theToken.slice(1,theToken.length-1);
    }
  }
}
</script>

<style lang="less">
.edit_container{
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  .ql-toolbar.ql-snow{
    display: flex;
    padding: 2px 10px;
    .ql-formats{
      margin: 0;
    }
  }
}
.ql-editor{
   min-height: 120px;
}
</style>